import React, { useEffect } from "react";
import { MDBRow, MDBCol, MDBCard, MDBCardBody } from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControl,
  FormControlLabel,
  InputLabel,
  Select,
  MenuItem,
  Slider,
  Switch,
  TextField,
  Stack,
  Autocomplete,
} from "@mui/material";

import Schools from "./Schools";

function AcademicProfile() {
  const [age, setAge] = React.useState("");
  const[transferyear,setTransferYear]=React.useState("");
  const[enroll, setEnroll] =React.useState("");
  const[student_percentile, setStudentPercentile] =React.useState("");
  const[gpa_type, setGpaType] =React.useState("");
  const[psat_enable, setPsatEnable] = React.useState(false);
  const[sat_enable, setsatEnable] = React.useState(false);
  const[act_enable, setactEnable] = React.useState(false);
  const[psat_math, setpsatMath] = React.useState(0);
  const[psat_verbal, setpsatVerbal] = React.useState(0);
  const[sat_math, setSatMath] = React.useState(0);
  const[sat_verbal, setSatVerbal] = React.useState(0);
  const[act_composite, setActComposite] = React.useState(0);
  const[act_math, setActMath] = React.useState(0);
  const[act_verbal, setActVerbal] = React.useState(0);

  const transferyearchange = (event) => {
    setTransferYear(event.target.value);
  };
  const enrollchange = (event) => {
    setEnroll(event.target.value);
  };
  const student_percentilechange = (event) => {
    setStudentPercentile(event.target.value);
  };
  const gpa_typechange = (event) => {
    setGpaType(event.target.value);
  };
  const transfer_yearchange = (event) => {
    setTransferYear(event.target.value);
  };

  const enablePsat = () => {
    if(psat_enable === true){
      setPsatEnable(false)
    }else{
      setPsatEnable(true)
    }
  }
  const enablesat = () => {
    if(sat_enable === true){
      setsatEnable(false)
    }else{
      setsatEnable(true)
    }
  }
  const enableact = () => {
    if(act_enable === true){
      setactEnable(false)
    }else{
      setactEnable(true)
    }
  }

  const graduation_years = [];
  for (let index = 0; index < 7; index++) {
    graduation_years.push(new Date().getFullYear() + index);
  }

  useEffect(()=>{
    if(psat_enable === false){
      document.getElementById('psat-score-math-visible').style.display = 'none';
      document.getElementById('psat-score-verbal-visible').style.display = 'none';
    }if(psat_enable === true){
      document.getElementById('psat-score-math-visible').style.display = 'block';
      document.getElementById('psat-score-verbal-visible').style.display = 'block';
    }
    if(sat_enable === false){
      document.getElementById('sat-score-math-visible').style.display = 'none';
      document.getElementById('sat-score-verbal-visible').style.display = 'none';
    }if(sat_enable === true){
      document.getElementById('sat-score-math-visible').style.display = 'block';
      document.getElementById('sat-score-verbal-visible').style.display = 'block';
    }
    if(act_enable === false){
      document.getElementById('act-score-composite-visible').style.display = 'none';
      document.getElementById('act-score-math-visible').style.display = 'none';
      document.getElementById('act-score-verbal-visible').style.display = 'none';
    }if(act_enable === true){
      document.getElementById('act-score-composite-visible').style.display = 'block';
      document.getElementById('act-score-math-visible').style.display = 'block';
      document.getElementById('act-score-verbal-visible').style.display = 'block';
    }
  });

  return (
    <div>
      <MDBCard>
        <MDBCardBody className="px-4">
          <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
            Academic Profile
          </h3>
          <MDBRow>
            <MDBCol md="8">
              <Stack spacing={3}>
                <Autocomplete
                  multiple
                  id="schools-highschools"
                  options={Schools.HighSchools}
                  getOptionLabel={(option) => option.NAME}
                  filterSelectedOptions
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Name of High School"
                      placeholder="Name of High School"
                    />
                  )}
                />
              </Stack>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="high-school-graduation-completion-year-select-label">
                  High School Graduation (12th grade)/completion year
                </InputLabel>
                <Select
                  labelId="high-school-graduation-completion-year-select-label"
                  id="high-school-graduation-completion-year-select"
                  label="High School Graduation (12th grade)/completion year "
                >
                  {graduation_years.map((year) => (
                    <MenuItem>{year}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="looking-to-enroll-in-select-label">
                  Looking to Enroll in
                </InputLabel>
                <Select
                  labelId="looking-to-enroll-in-label"
                  id="looking-to-enroll-in-select"
                  label="looking-to-enroll-in"
                >
                  <MenuItem value={1}>Spring {new Date().getFullYear()+1}</MenuItem>
                  <MenuItem value={2}>
                    Fall {new Date().getFullYear()+1}
                  </MenuItem>
                  <MenuItem value={3}>
                    Spring {new Date().getFullYear() + 2}
                  </MenuItem>
                  <MenuItem value={4}>
                    Fall {new Date().getFullYear() + 2}
                  </MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="enrolled-as-select-label">
                  ENROLLED AS
                </InputLabel>
                <Select
                  labelId="enrolled-as-select-label"
                  id="enrolled-as-select"
                  value={enroll}
                  label="ENROLLED AS"
                  onChange={enrollchange}
                >
                  <MenuItem value={10}>FIRST YEAR</MenuItem>
                  <MenuItem value={20}>TRANSFER</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="transfer-year-select-label">
                  Transfer Year
                </InputLabel>
                <Select
                  labelId="transfer-year-select-label"
                  id="transfer-year-select"
                  value={transferyear}
                  label="Transfer year"
                  onChange={transferyearchange}
                >
                  <MenuItem value={10}>Sophmore</MenuItem>
                  <MenuItem value={20}>Junior</MenuItem>
                  <MenuItem value={30}>Senior</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <br />

          <MDBRow>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="student-class-rank-select-label">
                  Student Class Rank (percentile)
                </InputLabel>
                <Select
                  labelId="student-class-rank-select-label"
                  id="student-class-rank-select"
                  value={student_percentile}
                  label="Student Class Rank (percentile)"
                  onChange={student_percentilechange}
                >
                  <MenuItem value={10}>Top 10%</MenuItem>
                  <MenuItem value={20}>Top Quarter</MenuItem>
                  <MenuItem value={30}>Top half</MenuItem>
                  <MenuItem value={40}>Bottom Half</MenuItem>
                  <MenuItem value={50}>Bottom Quarter</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="gpa-weighted-type-label">
                  GPA Weightage Type
                </InputLabel>
                <Select
                  labelId="gpa-weighted-type-label"
                  id="gpa-weighted-type"
                  value={gpa_type}
                  label="GPA Weightage Type"
                  onChange={gpa_typechange}
                >
                  <MenuItem value={1}>Weighted</MenuItem>
                  <MenuItem value={2}>UnWeighted</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <MDBRow>
                <MDBCol md="5">
                  <TextField
                    id="your-gpa-standard-basic"
                    type="number"
                    label="Your GPA"
                    variant="standard"
                    fullWidth
                  />
                </MDBCol>
                <MDBCol md="1">
                  <center><p style={{fontSize: '2vw'}}>/</p></center>
                </MDBCol>
                <MDBCol md="5">
                  <TextField
                    id="toatal-gpa-standard-basic"
                    type="number"
                    label="Total GPA"
                    variant="standard"
                    fullWidth
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="transfer-year-label">
                  Transfer Year
                </InputLabel>
                <Select
                  labelId="transfer-year-label"
                  id="transfer-year"
                  value={transferyear}
                  label="Transfer year"
                  onChange={transfer_yearchange }
                >
                  <MenuItem value={10}>Certificate</MenuItem>
                  <MenuItem value={20}>Associate Degree</MenuItem>
                  <MenuItem value={30}>Bachelor's Degree</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <p>
                  International Baccalauerate (IB)
                  <Switch labelId="international-baccalauerate-label" />
                </p>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <TextField
                id="predicted-ib-score"
                type="number"
                label="Predicted IB Score (Out of 45)"
                variant="standard"
                fullWidth
              />
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="3">
              <FormControlLabel
                control={<Checkbox onChange={enablePsat}/>}
                label="PSAT SCORE "
                labelPlacement="end"
                id="psat-score-enable"
              />
            </MDBCol>
            <MDBCol md="4" id="psat-score-math-visible">
                <TextField
                 
                    id="psat-score-math"
                    type="number"
                    label="PSAT MATH"
                    variant="standard"
                    fullWidth
                  />
            </MDBCol>
            <MDBCol md="4" id="psat-score-verbal-visible">
                <TextField
                    id="psat-score-verbal"
                    type="number"
                    label="PSAT VERBAL"
                    variant="standard"
                    fullWidth
                  />
            </MDBCol>            
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="3">
              <FormControlLabel
                control={<Checkbox onChange={enablesat}/>}
                label="SAT SCORE "
                labelPlacement="end"
                id="sat-score-enable"
              />
            </MDBCol>
            <MDBCol md="4" id="sat-score-math-visible">
                <TextField
                    id="sat-score-math"
                    type="number"
                    label="SAT MATH"
                    variant="standard"
                    fullWidth
                  />
            </MDBCol>
            <MDBCol md="4" id="sat-score-verbal-visible">
                <TextField
                    id="sat-score-verbal"
                    type="number"
                    label="SAT VERBAL"
                    variant="standard"
                    fullWidth
                  />
            </MDBCol>            
          </MDBRow>
          <br/>
          <MDBRow>
            <MDBCol md="3">
              <FormControlLabel
                control={<Checkbox onChange={enableact}/>}
                label="ACT SCORE "
                labelPlacement="end"
                id="act-score-enable"
              />
            </MDBCol>
            <MDBCol md="3" id="act-score-composite-visible">
                <TextField
                    id="act-score-composite"
                    type="number"
                    label="ACT Composite"
                    variant="standard"
                    fullWidth
                  />
            </MDBCol>
            <MDBCol md="3" id="act-score-math-visible">
                <TextField
                    id="act-score-math"
                    type="number"
                    label="ACT MATH"
                    variant="standard"
                    fullWidth
                  />
            </MDBCol>
            <MDBCol md="3" id="act-score-verbal-visible">
                <TextField
                    id="act-score-verbal"
                    type="number"
                    label="ACT VERBAL"
                    variant="standard"
                    fullWidth
                  />
            </MDBCol>            
          </MDBRow>
        </MDBCardBody>
      </MDBCard>
    </div>
  );
}
export default AcademicProfile;
