import React, { useState } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
} from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControlLabel,
  FormLabel,
  FormGroup,
  Radio,
  RadioGroup,
  Slider,
  Switch,
  TextField,
} from "@mui/material";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Autocomplete from "@mui/material/Autocomplete";

function FinancialInformation(){
    
    const [scholarships, setscholarships] = React.useState("");
    const [pay_college, setpay_college] = React.useState("");
    const [student_income, setstudent_income] = React.useState("");
    const [family_income, setfamily_income] = React.useState("");
    const [savedamt, setsavedamt] = React.useState("");
    const [monthlyamt, setmonthlyamt] = React.useState("");
    const [networth, setnetworth] = React.useState("");

  const [satScore, setSatScore] = useState(false);

  const scholarshipschange = (event) => {
    setscholarships(event.target.value);
  };

  const pay_collegechange = (event) => {
    setpay_college(event.target.value);
  };
  const student_incomechange = (event) => {
    setstudent_income(event.target.value);
  };
  const family_incomechange = (event) => {
    setfamily_income(event.target.value);
  };
  const savedamtchange = (event) => {
    setsavedamt(event.target.value);
  };
  const monthlyamtchange = (event) => {
    setmonthlyamt(event.target.value);
  };
  const networthchange = (event) => {
    setnetworth(event.target.value);
  };
 
 
    return(
        <div>
             <MDBCard>
          <MDBCardBody className="px-4">
            <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
              Financial Information
            </h3>
            <MDBRow>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <p>
                    Are you Planning to Apply for Financial Aid?
                    <Switch labelId="plan-for-financial-aid" defaultChecked />
                  </p>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="college-merit-scholarship">
                  How important is it to attend a college that offers merit scholarships?
                  </InputLabel>
                  <Select
                    labelId="college-merit-scholarship"
                    id="college-merit-scholarship"
                    value={scholarships}
                    label="How important is it to attend a college that offers merit scholarships?"
                    onChange={scholarshipschange}
                  >
                    <MenuItem value={10}>Very Important</MenuItem>
                    <MenuItem value={10}>Somewhat Important</MenuItem>
                    <MenuItem value={10}>Not At all Important</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="who-will-pay-for-college-label">
                    Who Will Pay For College?
                  </InputLabel>
                  <Select
                    labelId="who-will-pay-for-college-label"
                    id="who-will-pay-for-college"
                    value={pay_college}
                    label=" Who Will Pay For College?"
                    onChange={pay_collegechange}
                  >
                    <MenuItem value={10}>Student Only</MenuItem>
                    <MenuItem value={10}>Student And Family</MenuItem>
                    <MenuItem value={10}>Family Only</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
            <br/>
            <MDBRow>
            <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="student-expected-annual-income-label">
                  Student's Expected Annual Income
                  </InputLabel>
                  <Select
                    labelId="student-expected-annual-income-label"
                    id="student-expected-annual-income"
                    value={student_income}
                    label="Student Expected Annual Income"
                    onChange={student_incomechange}
                  >
                    <MenuItem value={10}>$0-$10,000</MenuItem>
                    <MenuItem value={10}>$10,001-$20,000</MenuItem>
                    <MenuItem value={10}>$21,000-30,000</MenuItem>
                    <MenuItem value={10}>$31,000-40,000</MenuItem>
                    <MenuItem value={10}>$41,000-50000</MenuItem>
                    <MenuItem value={10}> $50,000+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="gross-family-annual-household-income-label">
                  Gross family's annual household income from all sources
                  </InputLabel>
                  <Select
                    labelId="gross-family-annual-household-income-label"
                    id="gross-family-annual-household-income"
                    value={family_income}
                    label="Gross family annual household income from all sources"
                    onChange={family_incomechange}
                  >
                    <MenuItem value={10}>0 - 30000 </MenuItem>
                    <MenuItem value={20}>300001 - 48000</MenuItem>
                    <MenuItem value={30}>48001 - 75000</MenuItem>
                    <MenuItem value={40}>75001 - 110000</MenuItem>
                    <MenuItem value={50}>11000+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="total-networth-select-label">
                  Total Net Worth including retirement accounts and primary residence
                  </InputLabel>
                  <Select
                    labelId="total-networth-select-label"
                    id="total-networth-select"
                    value={networth}
                    label="Total Net Worth including retirement accounts and primary residence"
                    onChange={networthchange}
                  >
                    <MenuItem value={10}>0 - 10000</MenuItem>
                    <MenuItem value={20}>10001 - 500000</MenuItem>
                    <MenuItem value={30}>501000 - 1M</MenuItem>
                    <MenuItem value={40}>1M - 2M</MenuItem>
                    <MenuItem value={50}>2M+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
            <br/>
            <MDBRow>
            <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="amount-saved-for-college-select-label">
                  Amount saved for college (Including 529 accounts for US residents)
                  </InputLabel>
                  <Select
                    labelId="amount-saved-for-college-select-label"
                    id="amount-saved-for-college-select"
                    value={savedamt}
                    label="Amount saved for college (including 529 accounts for US residents)"
                    onChange={savedamtchange}
                  >
                    <MenuItem value={10}>0-$10,000 </MenuItem>
                    <MenuItem value={20}> $10,001-$25,000</MenuItem>
                    <MenuItem value={30}>$25,001-$50,000</MenuItem>
                    <MenuItem value={40}>$50,0001-$100,000</MenuItem>
                    <MenuItem value={50}> $100,001 - $200,000</MenuItem>
                    <MenuItem value={60}>$200,001-$400,000</MenuItem>
                    <MenuItem value={70}> $400,000+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="planned-monthly-contribution-select-label">
                  Planned monthly contributions towards college fund
                  </InputLabel>
                  <Select
                    labelId="planned-monthly-contribution-select-label"
                    id="planned-monthly-contribution-select"
                    value={monthlyamt}
                    label="Planned monthly contributions towards college fund"
                    onChange={monthlyamtchange}
                  >
                    <MenuItem value={10}>$0-$100 </MenuItem>
                    <MenuItem value={20}>$101-$250</MenuItem>
                    <MenuItem value={30}>$251-$500</MenuItem>
                    <MenuItem value={40}>$501-$1,000</MenuItem>
                    <MenuItem value={50}>$1,001-$2,000</MenuItem>
                    <MenuItem value={60}>$2,001-$4,000</MenuItem>
                    <MenuItem value={70}>$4,000+</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
        </div>
    );
}
export default FinancialInformation;